.worksBlock {
    height: 100vh;
    background-color: #cfa7fc;
}

.worksContainer {
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.works{
    width: 100%;
    border: 1px solid;
    display: flex;
    justify-content: space-between;
}

@media screen and (max-width: 768px) {
    .works {
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
    }
}